export default {
	data() {
		return {
			tel: ""
		};
	},
	computed: {
	},
	methods: {
		LoginAccPws(){
			uni.navigateTo({
				url: '../login-accpwd/login-accpwd'
			})
		},
		Login(){
			if(!(/^[1][3,4,5,7,8][0-9]{9}$/.test(this.tel))){
				uni.showToast({
					icon: "none",
				    title: '请输入正确手机号',
				    duration: 2000
				});
			} else {
				uni.navigateTo({
					url: "../login-verify/login-verify"
				})
			}
			
		},
		ClearTel(){
			this.tel = ''
		},
		ViewIntro(){
			uni.navi
		},
	}
}
